        id: ''
      },
      addTableDatasale: {
        typesale: 3,
        phone: '',
        avator: 'https://abc.yongweisoft.cn./technicianHeadImg/simge/b4cebefeab694f37d4340f0759ec8588.jpg',
        level: '',
        sex: 1,
        level_imgurl: '',
        address: '',
        status: 1,
        uname: '',
        sale_products: [],
        sale_region: '',
        label: '',
        aid: '',
        id: ''
      },
      addTableDataagent: {
        typeagent: 4,
        phone: '',
