        phone: '',
        avator: 'https://abc.yongweisoft.cn./technicianHeadImg/simge/b4cebefeab694f37d4340f0759ec8588.jpg',
        level: '',
        sex: 1,
        level_imgurl: 'https://abc.yongweisoft.cn./technicianHeadImg/simge/b4cebefeab694f37d4340f0759ec8588.jpg',
        address: '',
        status: 1,
        label: '',
        aid: '-1',
        id: ''
      },
      center: {
        lng: 116.404,
        lat: 39.915
      },
      zoom: 15,
      rules: {
        name: [{ required: true, message: '请输入用户姓名', trigger: 'blur' }],
        phone: [{ required: true, message: '请输入电话号码', trigger: 'blur' }],
        level: [{ required: true, message: '请输入等级', trigger: 'blur' }],
        address: [{ required: true, message: '请输入用户地址', trigger: 'blur' }]
      },
      rulesdrug: {
        phone: [{ required: true, message: '请输入电话号码', trigger: 'blur' }],
        uname: [{ required: true, message: '请输入负责人名字', trigger: 'blur' }],
        level: [{ required: true, message: '请输入等级', trigger: 'blur' }],
        be_good_at: [{ required: true, message: '请输入擅长治疗的病', trigger: 'blur' }],
        shop_name: [{ required: true, message: '请输入药店名', trigger: 'blur' }],
        licence_imgurl: [{ required: true, message: '请上传营业执照', trigger: 'blur' }],
        longitude: [
          { required: true, message: '请选择地图的经度', trigger: 'change' }
        ],
        latitude: [
          { required: true, message: '请选择地图的纬度', trigger: 'change' }
        ],
        address: [{ required: true, message: '请选择地址', trigger: 'change' }]
      },
      rulesale: {
        phone: [{ required: true, message: '请输入电话号码', trigger: 'blur' }],
        uname: [{ required: true, message: '请输入负责人名字', trigger: 'blur' }],
        level: [{ required: true, message: '请输入等级', trigger: 'blur' }],
        sale_region: [{ required: true, message: '请输入售卖区域', trigger: 'blur' }],
        sale_products: [
          { required: true, message: '请选择售卖的药品', trigger: 'blur' }
        ],
        address: [{ required: true, message: '请输入地址', trigger: 'blur' }]
      },
      rulesagent: {
        phone: [{ required: true, message: '请输入电话号码', trigger: 'blur' }],
        uname: [{ required: true, message: '请输入负责人名字', trigger: 'blur' }],
        level: [{ required: true, message: '请输入等级', trigger: 'blur' }],
        agent_region: [{ required: true, message: '请输入售卖区域', trigger: 'blur' }],
        agent_products: [
          { required: true, message: '请选择售卖的药品', trigger: 'blur' }
        ],
        address: [{ required: true, message: '请输入地址', trigger: 'blur' }]
      },
    }
  },
  activated () {
    this.getList()
  },
  methods: {
    syncCenterAndZoom (e) {
      const { lng, lat } = e.target.getCenter()
      this.center.lng = lng
      this.center.lat = lat
      this.zoom = e.target.getZoom()
    },
    bmaplist () {
      var _this = this
      var longitude = 104.0691640252
      var latitude = 30.6012017471
      var map = new BMap.Map('container')
      map.setDefaultCursor('crosshair')
      map.enableScrollWheelZoom()
      var point = new BMap.Point(longitude, latitude)
      map.centerAndZoom(point, 13)
      var gc = new BMap.Geocoder()
      map.addControl(new BMap.NavigationControl())
      map.addControl(new BMap.OverviewMapControl())
      map.addControl(new BMap.ScaleControl())
      map.addControl(new BMap.MapTypeControl())
      map.addControl(new BMap.CopyrightControl())
      var marker = new BMap.Marker(point)
      map.addOverlay(marker)
      marker.enableDragging()
      function showLocationInfo (pt, rs) {
        var opts = {
          title: '当前位置'
        }
        var addComp = rs.addressComponents
        var addr =
          addComp.province +
          addComp.city +
          addComp.district +
          addComp.street +
          addComp.streetNumber
        var infoWindow = new BMap.InfoWindow(addr, opts)
        marker.openInfoWindow(infoWindow)
        _this.addTableDatadrug.address =
          addComp.province +
          addComp.city +
          addComp.district +
          addComp.street +
          addComp.streetNumber
      }
      map.addEventListener('click', function (e) {
        var marker = new BMap.Marker(e.point)
        _this.addTableDatadrug.longitude = e.point.lng
        _this.addTableDatadrug.latitude = e.point.lat
        gc.getLocation(e.point, function (rs) {
          showLocationInfo(e.point, rs)
        })
        map.clearOverlays(marker)
        map.addOverlay(marker)
      })
      var traffic = new BMap.TrafficLayer()
      map.addTileLayer(traffic)
    },
    cancelForm (formName) {
      this.dialogVisible = false
      this.dialogVisibledrug = false
      this.dialogVisiblesale = false
      this.$refs[formName].resetFields()
    },
    submitForm (formName) {
      let _this = this
      _this.$refs[formName].validate((valid) => {
        if (valid) {
          this.sureLoading = true
          let da = {
            tag: 'member',
            op: '',
            condition: {
              id: this.addTableData.id
            },
            data: {
              type: '1',
              openid: '',
              name: this.addTableData.name,
              phone: this.addTableData.phone,
              avator: this.addTableData.avator,
              level: '1',
              sex: this.addTableData.sex,
              level_imgurl: this.addTableData.level_imgurl,
              address: this.addTableData.address,
              status: this.addTableData.status,
              label: ''
            }
          }
          if (_this.addTableData.aid === '-1') {
            da.op = 'add'
          } else {
            da.op = 'update'
          }
          this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(da), {
            headers: {'Content-Type': 'application/x-www-form-urlencoded'}
          }).then(res => {
            let da = res.data
            if (da.err > 0) {
              this.getList()
              this.dialogVisible = false
              this.sureLoading = false
              _this.$Notice.success({title: da.message})
            } else {
              this.sureLoading = false
              _this.$Notice.warning({title: da.message})
            }
          }).catch(function () {
            _this.$Notice.error({title: '请求失败，请重试'})
          })
        } else {
          return false
        }
      })
    },
    // 新增编辑药店信息
    submitFormdrug (formName) {
      let _this = this
      _this.$refs[formName].validate((valid) => {
        if (valid) {
          this.sureLoading = true
          let da = {
            tag: 'member',
            op: '',
            condition: {
              id: this.addTableDatadrug.id
            },
            data: {
              type: '2',
              uname: this.addTableDatadrug.uname,
              phone: this.addTableDatadrug.phone,
              avator: this.addTableDatadrug.avator,
              level: '1',
              sex: this.addTableDatadrug.sex,
              level_imgurl: this.addTableDatadrug.level_imgurl,
              status: this.addTableDatadrug.status,
              shop_name: this.addTableDatadrug.shop_name,
              longitude: this.addTableDatadrug.longitude,
              latitude: this.addTableDatadrug.latitude,
              address: this.addTableDatadrug.address,
              licence_imgurl: this.addTableDatadrug.licence_imgurl,
              be_good_at: this.addTableDatadrug.be_good_at,
              label: ''
            }
          }
          if (_this.addTableDatadrug.aid === '-1') {
            da.op = 'add'
          } else {
            da.op = 'update'
          }
          this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(da), {
            headers: {'Content-Type': 'application/x-www-form-urlencoded'}
          }).then(res => {
            let da = res.data
            if (da.err > 0) {
              this.getList()
              this.dialogVisibledrug = false
              this.sureLoading = false
              _this.$Notice.success({title: da.message})
            } else {
              this.sureLoading = false
              _this.$Notice.warning({title: da.message})
            }
          }).catch(function () {
            _this.$Notice.error({title: '请求失败，请重试'})
          })
        } else {
          return false
        }
      })
    },
    submitFormsale (formName) {
      let _this = this
      _this.$refs[formName].validate((valid) => {
        if (valid) {
          this.sureLoading = true
          let da = {
            tag: 'member',
            op: '',
            condition: {
              id: this.addTableDatasale.id
            },
            data: {
              type: '3',
              uname: this.addTableDatasale.uname,
              phone: this.addTableDatasale.phone,
              avator: this.addTableDatasale.avator,
              level: '1',
              sex: this.addTableDatasale.sex,
              level_imgurl: this.addTableDatasale.level_imgurl,
              address: '',
              status: this.addTableDatasale.status,
              label: '',
              sale_products: this.addTableDatasale.sale_products,
              sale_region: this.addTableDatasale.sale_region
            }
          }
          if (_this.addTableDatasale.aid === '-1') {
            da.op = 'add'
          } else {
            da.op = 'update'
          }
          this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(da), {
            headers: {'Content-Type': 'application/x-www-form-urlencoded'}
          }).then(res => {
            let da = res.data
            if (da.err > 0) {
              this.getList()
              this.dialogVisiblesale = false
              this.sureLoading = false
              _this.$Notice.success({title: da.message})
            } else {
              this.sureLoading = false
              _this.$Notice.warning({title: da.message})
            }
          }).catch(function () {
            _this.$Notice.error({title: '请求失败，请重试'})
          })
        } else {
          return false
        }
      })
    },
    submitFormagent (formName) {
      let _this = this
      _this.$refs[formName].validate((valid) => {
        if (valid) {
          this.sureLoading = true
          let da = {
            tag: 'member',
            op: '',
            condition: {
              id: this.addTableDataagent.id
            },
            data: {
              type: '4',
              uname: this.addTableDataagent.uname,
              phone: this.addTableDataagent.phone,
              avator: this.addTableDataagent.avator,
              level: '1',
              sex: this.addTableDataagent.sex,
              level_imgurl: this.addTableDataagent.level_imgurl,
              address: '',
              status: this.addTableDataagent.status,
              label: '',
              agent_products: this.addTableDataagent.agent_products,
              agent_region: this.addTableDataagent.agent_region
            }
          }
          if (_this.addTableDataagent.aid === '-1') {
            da.op = 'add'
          } else {
            da.op = 'update'
          }
          this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(da), {
            headers: {'Content-Type': 'application/x-www-form-urlencoded'}
          }).then(res => {
            let da = res.data
            if (da.err > 0) {
              this.getList()
              this.dialogVisibleagent = false
              this.sureLoading = false
              _this.$Notice.success({title: da.message})
            } else {
              this.sureLoading = false
              _this.$Notice.warning({title: da.message})
            }
          }).catch(function () {
            _this.$Notice.error({title: '请求失败，请重试'})
          })
        } else {
          return false
        }
      })
    },
    add (formName) {
      this.dialogVisible = true
      this.addTableData = {
        name: '',
        type: 1,
        phone: '',
        avator: 'https://abc.yongweisoft.cn./technicianHeadImg/simge/b4cebefeab694f37d4340f0759ec8588.jpg',
        level: '',
        sex: 1,
        level_imgurl: 'https://abc.yongweisoft.cn./technicianHeadImg/simge/b4cebefeab694f37d4340f0759ec8588.jpg',
        address: '',
        status: 1,
        label: '',
        aid: '-1',
        id: ''
      }
      this.$refs[formName].resetFields()
    },
    adddrug (formName) {
      this.dialogVisibledrug = true
      this.addTableDatadrug = {
        typedrug: 2,
        phone: '',
        avator: 'https://abc.yongweisoft.cn./technicianHeadImg/simge/b4cebefeab694f37d4340f0759ec8588.jpg',
        level: '',
        sex: 1,
        level_imgurl: 'https://abc.yongweisoft.cn./technicianHeadImg/simge/b4cebefeab694f37d4340f0759ec8588.jpg',
        address: '',
        status: 1,
        uname: '',
        shop_name: '',
        longitude: '',
        latitude: '',
        licence_imgurl: 'https://abc.yongweisoft.cn./technicianHeadImg/simge/b4cebefeab694f37d4340f0759ec8588.jpg',
        be_good_at: '',
        label: '',
        aid: '-1',
        id: ''
      }
      setTimeout(() => {
        this.bmaplist()
      }, 600)
      this.$refs[formName].resetFields()
    },
    addsale (formName) {
      this.dialogVisiblesale = true
      this.addTableDatasale = {
        sale_products: [],
        typesale: 3,
        phone: '',
        avator: 'https://abc.yongweisoft.cn./technicianHeadImg/simge/b4cebefeab694f37d4340f0759ec8588.jpg',
        level: '',
        sex: 1,
        level_imgurl: 'https://abc.yongweisoft.cn./technicianHeadImg/simge/b4cebefeab694f37d4340f0759ec8588.jpg',
        address: '',
        status: 1,
        label: '',
        aid: '-1',
        id: ''
      }
      this.saleList()
    },
    addagent (formName) {
      this.dialogVisibleagent = true
      this.addTableDataagent = {
        agent_products: [],
        typesagent: 4,
        phone: '',
        avator: 'https://abc.yongweisoft.cn./technicianHeadImg/simge/b4cebefeab694f37d4340f0759ec8588.jpg',
        level: '',
        sex: 1,
        level_imgurl: 'https://abc.yongweisoft.cn./technicianHeadImg/simge/b4cebefeab694f37d4340f0759ec8588.jpg',
        address: '',
        status: 1,
        label: '',
        aid: '-1',
        id: ''
      }
      this.saleList()
      this.$refs[formName].resetFields()
    },
    addsure () {
      // alert(this.shopname)
      this.dialogVisiblecase = true
    },
    editor (index, id, list, formName) {
      this.dialogVisible = true
      this.sureLoading = false
      this.addTableData = Object.assign({}, list[index])
      this.$refs[formName].resetFields()
    },
    editordrug (index, id, list, formName) {
      this.dialogVisibledrug = true
      this.sureLoading = false
      this.addTableDatadrug = Object.assign({}, list[index])
      this.$refs[formName].resetFields()
    },
    editorsale (index, id, list, formName) {
      this.dialogVisiblesale = true
      this.sureLoading = false
      this.addTableDatasale = Object.assign({}, list[index])
      this.saleList()
      this.$refs[formName].resetFields()
    },
    editoragent (index, id, list, formName) {
      this.dialogVisibleagent = true
      this.sureLoading = false
      this.addTableDataagent = Object.assign({}, list[index])
      this.saleList()
      this.$refs[formName].resetFields()
    },
    handleSizeChange (size) {
      this.pagesize = size
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.getList()
    },
    getList (isSearch) {
      let _this = this
      if (isSearch) {
        this.currentPage = 1
      }
      var pa = this.currentPage
      let d = {
        tag: 'member',
        op: 'getlist',
        condition: {
          'type': this.formInline.type
        }
      }
      if (this.formInline.type === 1) {
        this.patient = true
      } else {
        this.patient = false
      }
      if (this.formInline.type === 2) {
        this.drugshop = true
      } else {
        this.drugshop = false
      }
      if (this.formInline.type === 3 || this.formInline.type === 4) {
        this.saleagent = true
      } else {
        this.saleagent = false
      }
      this.$axios.post(domain.testUrl + '/admin/member/index?' + 'limit=10&page=' + pa, qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData = da.data.list
          this.total = da.data.total
          this.loading = false
        } else {
          this.loading = false
          this.tableData = []
          this.total = 0
          this.$Notice.warning({title: da.message})
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    addcase () {
      let _this = this
      this.shopname = ''
      var pa = this.currentPage
      let d = {
        tag: 'member',
        op: 'getlist',
        condition: {
          'type': '2'
        }
      }
      this.$axios.post(domain.testUrl + '/admin/member/index?' + 'limit=10&page=' + pa, qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableDataShopname = da.data.list
        } else {
          this.$Notice.warning({title: da.message})
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    saleList (isSearch) {
      let d = {
        tag: 'product',
        op: 'getlist',
        condition: {
          'name': this.formInline.name,
          'sn': this.formInline.sn,
          'enddate': this.formInline.enddate,
          'stock': this.formInline.stock
        }
      }
      this.$axios.post(domain.testUrl + '/admin/product/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableDatagood = da.data.list
          this.loading = false
        } else {
          this.$Notice.warning({title: da.message})
          this.loading = false
        }
      }).catch(function () {
        this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    // 详情
    detail (id) {
      if (this.formInline.type === 1) {
        this.patientdetial = true
        this.allname = false
        this.sg = false
      } else {
        this.patientdetial = false
      }
      if (this.formInline.type === 2) {
        this.drug = true
        this.allname = true
        this.sg = false
      } else {
        this.drug = false
      }
      if (this.formInline.type === 3) {
        this.saledetail = true
        this.allname = true
        this.sg = true
      } else {
        this.saledetail = false
      }
      if (this.formInline.type === 4) {
        this.agentdetail = true
        this.allname = true
        this.sg = true
      } else {
        this.agentdetail = false
      }
      let d = {
        tag: 'member',
        op: 'getone',
        condition: {
          'id': id
        }
      }
      this.dialogdetail = true
      this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableDatadetail = da.data
        } else {
          this.loading = false
        }
      }).catch(function () {
        this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    carse (id) {

      // let d = {
      //   tag: 'member',
      //   op: 'case_history',
      //   condition: {
      //     'id': id
      //   }
      // }
      // this.dialogcase = true
      // this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
      //   headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      // }).then(res => {
      //   let da = res.data
      //   if (da.err > 0) {
      //     this.tableDatacase = da.data.goodsinfo.list
      //     this.totaldetail = da.data.goodsinfo.total
      //   } else {
      //     this.loading = false
      //   }
      // }).catch(function () {
      //   this.$Notice.error({title: '请求失败，请重试'})
      // })
    },
    changeState (id, status) {
      let _this = this
      if (status === 1) {
        status = 0
      } else {
        status = 1
      }
      let d = {
        tag: 'member',
        op: 'state',
        condition: {
          id: id
        },
        data: {
          status: status
        }
      }
      _this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          _this.getList()
          _this.$Notice.success({title: da.message})
        } else {
          _this.$Notice.warning({title: da.message})
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    }
  }
}
</script>



